"use client"

import type React from "react"
import { useEffect, useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Datacenter } from "@/lib/types"

interface DatacenterFormDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  datacenter?: Datacenter | null
  onSubmit: (data: Partial<Datacenter>) => void
}

export function DatacenterFormDialog({ open, onOpenChange, datacenter, onSubmit }: DatacenterFormDialogProps) {
  const [formData, setFormData] = useState({
    name: "",
    provider: "proxmox",
    location: "",
    apiEndpoint: "",
    infrastructureType: "cloud",
    managementType: "managed",
  })

  // Reset or pre-fill form when dialog opens or datacenter changes
  useEffect(() => {
    if (datacenter) {
      setFormData({
        name: datacenter.name,
        provider: datacenter.provider,
        location: datacenter.location,
        apiEndpoint: datacenter.apiEndpoint,
        infrastructureType: datacenter.infrastructureType || "cloud",
        managementType: datacenter.managementType || "managed",
      })
    } else {
      setFormData({
        name: "",
        provider: "proxmox",
        location: "",
        apiEndpoint: "",
        infrastructureType: "cloud",
        managementType: "managed",
      })
    }
  }, [datacenter, open])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit(formData as Partial<Datacenter>)
    onOpenChange(false)
  }

  const isEdit = !!datacenter

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[525px] bg-card/95 backdrop-blur-xl border-white/10">
        <form onSubmit={handleSubmit}>
          <DialogHeader>
            <DialogTitle>{isEdit ? "Edit Datacenter" : "Add New Datacenter"}</DialogTitle>
            <DialogDescription>
              {isEdit ? "Update datacenter details and configuration." : "Connect a new datacenter to manage servers."}
            </DialogDescription>
          </DialogHeader>

          <div className="grid gap-4 py-4">
            <div className="grid grid-cols-2 gap-4">
              <div className="grid gap-2">
                <Label htmlFor="name">Name</Label>
                <Input
                  id="name"
                  placeholder="DC-US-EAST-1"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                  className="bg-secondary/50 border-white/10"
                />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="location">Location</Label>
                <Input
                  id="location"
                  placeholder="Virginia, USA"
                  value={formData.location}
                  onChange={(e) => setFormData({ ...formData, location: e.target.value })}
                  required
                  className="bg-secondary/50 border-white/10"
                />
              </div>
            </div>

            <div className="grid gap-2">
              <Label htmlFor="provider">Provider</Label>
              <Select
                value={formData.provider}
                onValueChange={(value) => setFormData({ ...formData, provider: value })}
              >
                <SelectTrigger id="provider" className="bg-secondary/50 border-white/10">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="proxmox">Proxmox VE</SelectItem>
                  <SelectItem value="vmware">VMware vSphere</SelectItem>
                  <SelectItem value="openstack">OpenStack</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="grid gap-2">
                <Label htmlFor="infra-type">Infrastructure</Label>
                <Select
                  value={formData.infrastructureType}
                  onValueChange={(value) => setFormData({ ...formData, infrastructureType: value })}
                >
                  <SelectTrigger id="infra-type" className="bg-secondary/50 border-white/10">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="cloud">Cloud / Virtualized</SelectItem>
                    <SelectItem value="baremetal">Bare Metal</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid gap-2">
                <Label htmlFor="mgmt-type">Management</Label>
                <Select
                  value={formData.managementType}
                  onValueChange={(value) => setFormData({ ...formData, managementType: value })}
                >
                  <SelectTrigger id="mgmt-type" className="bg-secondary/50 border-white/10">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="managed">Fully Managed</SelectItem>
                    <SelectItem value="manual">Manual Maintenance</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="grid gap-2">
              <Label htmlFor="apiEndpoint">API Endpoint</Label>
              <Input
                id="apiEndpoint"
                type="url"
                placeholder="https://proxmox.example.com:8006"
                value={formData.apiEndpoint}
                onChange={(e) => setFormData({ ...formData, apiEndpoint: e.target.value })}
                required
                className="bg-secondary/50 border-white/10 font-mono text-xs"
              />
            </div>

            {!isEdit && (
              <div className="grid gap-2">
                <Label htmlFor="apiToken">API Token</Label>
                <Input
                  id="apiToken"
                  type="password"
                  placeholder="PVEAPIToken=..."
                  className="bg-secondary/50 border-white/10"
                />
                <p className="text-[10px] text-muted-foreground">Only required for initial connection verification.</p>
              </div>
            )}
          </div>

          <DialogFooter>
            <Button type="button" variant="ghost" onClick={() => onOpenChange(false)}>
              Cancel
            </Button>
            <Button type="submit" className="glow-cyan">{isEdit ? "Save Changes" : "Connect Datacenter"}</Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}
